.footer__container {
  display: grid;
  grid-template-columns: min-content 1fr minmax(min-content, 115px);
  align-items: center;
  padding: 50px 20px; }
  @media (min-width: 768px) and (max-width: 1023px) {
    .footer__container {
      padding: 50px 20px; } }
  @media (min-width: 320px) and (max-width: 767px) {
    .footer__container {
      display: block;
      padding: 40px 10px; } }

/* ============ footer__logo */
/* Основные стили по стилизации в файле logo.scss */
@media (min-width: 320px) and (max-width: 767px) {
  .footer__logo {
    margin: 2px 0 21px; } }

@media (min-width: 1023px) {
  .footer__logo .logo__icon:hover,
  .footer__logo .logo__icon:focus {
    fill: #00e7ff; }
  .footer__logo .logo__icon:active {
    fill: #0d7aee; } }

.footer__logo .logo__icon:active {
  fill: #0d7aee; }

/* ============ footer__nav */
.footer__nav {
  margin-right: 20px; }
  @media (min-width: 320px) and (max-width: 767px) {
    .footer__nav {
      margin: 2px 0 23px; } }

.footer__nav .nav__list {
  margin: 0; }
  @media (max-width: 767px) {
    .footer__nav .nav__list {
      display: block; } }

@media (min-width: 768px) and (max-width: 1023px) {
  .footer__nav .nav__item {
    margin: 0 14px 5px 0; } }

@media (min-width: 320px) and (max-width: 767px) {
  .footer__nav .nav__item {
    margin: 0 0 8px 0; }
    .footer__nav .nav__item:last-child {
      margin: 0 0 0 0; } }

.footer__nav a {
  position: relative;
  color: #f9fbfd;
  padding: 5px 0; }
  @media (min-width: 1023px) {
    .footer__nav a::after {
      content: "";
      display: block;
      position: absolute;
      right: 0;
      bottom: -3px;
      width: 0;
      height: 2px;
      background-color: #9ab3ff;
      transition: width 0.6s ease; }
    .footer__nav a:hover::after, .footer__nav a:focus::after {
      content: "";
      width: 100%;
      left: 0;
      background-color: #ffffff; } }
  .footer__nav a:active {
    color: #75dfff; }
  @media (min-width: 768px) and (max-width: 1023px) {
    .footer__nav a {
      font-size: 12px;
      line-height: 15px;
      padding: 10px 10px 10px 0;
      color: #f9fbfd; } }
  @media (min-width: 320px) and (max-width: 767px) {
    .footer__nav a {
      font-size: 12px;
      line-height: 15px;
      padding: 10px 10px 10px 0;
      color: #f9fbfd; } }

.social {
  line-height: 0;
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  justify-content: flex-end; }
  @media (max-width: 600px) {
    .social {
      justify-content: center; } }
  @media (max-width: 320px) {
    .social {
      justify-content: flex-start; } }

.social__item {
  margin: 0 27px 15px 0; }
  @media (min-width: 768px) and (max-width: 1023px) {
    .social__item {
      margin: 0 18px 15px 0; } }
  @media (min-width: 320px) and (max-width: 767px) {
    .social__item {
      margin: 0 23px 15px 0; } }
  .social__item:last-child {
    margin-right: 0; }
  @media (max-width: 600px) {
    .social__item:last-child {
      margin-right: 15px; } }

.social__link {
  display: inline-block;
  vertical-align: middle; }
  .social__link--facebook {
    width: 11px;
    height: 20px; }
    @media (max-width: 767px) {
      .social__link--facebook {
        width: 9px;
        height: 16px; } }
  .social__link--instagram {
    width: 21px;
    height: 20px; }
    @media (max-width: 767px) {
      .social__link--instagram {
        width: 16px;
        height: 16px; } }
  .social__link--twitter {
    width: 20px;
    height: 17px; }
    @media (max-width: 767px) {
      .social__link--twitter {
        width: 16px;
        height: 13px; } }

/* Иконке задаем 100% ширины и высоты, чтобы не загружать разные размеры под разные устройства, а ссылке в которую обернута svg - фактические размеры иконки */
.social__icon {
  width: 100%;
  height: 100%;
  fill: #f9fbfd; }
  @media (min-width: 1023px) {
    .social__icon:hover, .social__icon:focus {
      fill: #00acee; }
    .social__icon:active {
      fill: #2e80fd; } }
  .social__icon:active {
    fill: #2e80fd; }

